import React from 'react';
import AppIcon from "src/components/App/Icon/AppIcon";
import {ReactComponent as CloseIcon} from "src/assets/icons/close.svg";

import classes from "./AppModal.module.scss";

const AppModal = (props) => (
    <div className={classes.modalContainer}>
        <div className={classes.modalOverlay} onClick={props.closeAction}/>
        <div className={classes.modal}>
            {props.children}
            <AppIcon icon={CloseIcon} className={classes.closeButton} onClick={props.closeAction}/>
        </div>
    </div>
);

export default AppModal;
