import React, {Component} from 'react';

import {connect} from "react-redux";
import {ReactComponent as AddIcon} from "src/assets/icons/add.svg";
import AppButton from "src/components/App/Button/AppButton";

class CollectionItemSearchActions extends Component {
    render() {
        if (!this.props.item) {
            return null;
        }
        return (
            <>
                <AppButton full={1} icon={AddIcon} onClick={() => {
                    this.props.addItem(this.props.item);
                }}>
                    Add to NASA collection
                </AppButton>
            </>
        );
    }
}

const mapDispatchToProps = dispatch => ({
    addItem: (item) => dispatch({type: 'ADD_ITEM', item: item})
});

export default connect(null, mapDispatchToProps)(CollectionItemSearchActions);
