import React, {Component} from 'react';
import {connect} from "react-redux";
import CollectionItem from "src/components/Collection/Item/CollectionItem";

import classes from "./CollectionList.module.scss";

class CollectionList extends Component {
    render() {
        const addedItemsMap = this.getAddedItems();
        return (
            <div className={classes.list}>
                {this.props.items.map((item) => {
                    const itemId = item.data[0].nasa_id;
                    const isAdded = addedItemsMap[itemId];
                    if (this.props.hideAddedItems && isAdded) {
                        return null;
                    } else {
                        return <CollectionItem item={item}
                                               key={itemId}
                                               isAddedToCollection={addedItemsMap[itemId]}/>
                    }
                })}
            </div>
        );
    }

    /**
     * get Mapping for added items
     */
    getAddedItems() {
        return Object.assign({}, ...this.props.addedItems.map(item => {
            return {
                [item.data[0].nasa_id]: 1
            };
        }));
    }
}

const mapStateToProps = state => ({
    addedItems: state,
});

export default connect(mapStateToProps)(CollectionList);
