import React, {Component} from 'react';
import AppIcon from "src/components/App/Icon/AppIcon";
import classes from './AppButton.module.scss';
import {Link} from 'react-router-dom';
import className from "classnames";

export default class AppButton extends Component {
    render() {
        let icon = null;
        if (this.props.icon) {
            icon = (<AppIcon icon={this.props.icon} className={classes.buttonIcon}/>);
        }

        const btnClass = className({
            [classes.button]: true,
            [classes.primary]: this.props.primary,
            [classes.full]: this.props.full,
        }, this.props.className);

        if (this.props.to) {
            return (
                <Link {...{...this.props, className: btnClass}}>
                    {icon}
                    <span>{this.props.children}</span>
                </Link>
            );
        } else {
            return (
                <button {...{...this.props, className: btnClass}}>
                    {icon}
                    <span>{this.props.children}</span>
                </button>
            );
        }
    }
};
