import React, {Component} from 'react';
import AppImage from "src/components/App/Image/AppImage";
import AppModal from "src/components/App/Modal/AppModal";
import AppRow from "src/components/App/Row/AppRow";
import CollectionItemEditForm from "src/components/Collection/Item/EditForm/CollectionItemEditForm";
import CollectionItemListActions from "src/components/Collection/Item/ListActions/CollectionItemListActions";
import CollectionItemSearchActions from "src/components/Collection/Item/SearchActions/CollectionItemSearchActions";
import classes from "./CollectionItem.module.scss";
import moment from "moment";

class CollectionItem extends Component {
    state = {
        isEditing: false
    };

    render() {
        const data = this.getData();

        return (
            <>
                <article className={classes.item}>
                    <div className={classes.imageContainer}>
                        <AppImage src={this.getImage()} alt={data.title} className={classes.image}/>
                    </div>
                    <AppRow className={classes.subTitle}>
                        <span title={data.location} className={classes.location}>{data.location}</span>
                        <span className={classes.date}>{this.getDate()}</span>
                    </AppRow>
                    <div className={classes.description}>
                        <h1 className={classes.title}>{this.getTitle()}</h1>
                        {this.getDescription()}
                    </div>
                    <div className={classes.cta}>
                        {this.getItemAction(this.props.isAddedToCollection)}
                    </div>
                </article>
                {this.getEditModal()}
            </>
        );
    }

    getEditModal() {
        if (this.state.isEditing) {
            return (
                <AppModal closeAction={this.closeEditForm.bind(this)}>
                    <CollectionItemEditForm item={this.getItem()} onSave={this.closeEditForm.bind(this)}/>
                </AppModal>
            );
        } else {
            return null;
        }
    }

    getItem() {
        return this.props.item;
    }

    getData() {
        return this.getItem().data[0] || {};
    }

    getItemAction(isInCollection) {
        const item = this.getItem();
        if (isInCollection) {
            return <CollectionItemListActions item={item}
                                              editAction={this.edit.bind(this)}
                                              isFavorite={item.isFavorite}/>;
        } else {
            return <CollectionItemSearchActions item={item}/>
        }
    }

    getImage() {
        if (this.props.item.links) {
            return this.props.item.links[0].href;
        }
        return "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAADUlEQVR42mNcvHh7PQAGbAJ+GNbQTgAAAABJRU5ErkJggg==";
    }

    getDate() {
        return moment(this.getData().date_created).format('D MMM,YYYY');
    }

    getTitle() {
        return CollectionItem.cutString(this.getData().title, 60);
    }

    getDescription() {
        // Remove HTML tags
        const tmp = document.createElement("DIV");
        tmp.innerHTML = this.getData().description;
        return CollectionItem.cutString(tmp.textContent || tmp.innerText || "", 200);
    }

    static cutString(text, char) {
        return text ? text.length < char ? text : text.slice(0, char) + "..." : "";
    }

    edit() {
        this.setState({
            isEditing: true,
        });
    }

    closeEditForm() {
        this.setState({
            isEditing: false,
        });
    }
}

export default CollectionItem;
