import React, {Component} from 'react';

import {connect} from "react-redux";
import AppButton from "src/components/App/Button/AppButton";
import AppIcon from "src/components/App/Icon/AppIcon";

import {ReactComponent as AddFavoriteIcon} from "src/assets/icons/heart.svg";
import {ReactComponent as FavoriteIcon} from "src/assets/icons/heart-filled.svg";
import {ReactComponent as DeleteIcon} from "src/assets/icons/trash-bin.svg";
import {ReactComponent as EditIcon} from "src/assets/icons/pen.svg";

import classes from "./CollectionItemListActions.module.scss";

class CollectionItemListActions extends Component {
    render() {
        if (!this.props.item) {
            return null;
        }
        const itemId = this.props.item.data[0].nasa_id;
        const favoriteIcon = this.props.isFavorite ? (
            <AppIcon icon={FavoriteIcon} className={classes.favorite}/>
        ) : (
            <AppIcon icon={AddFavoriteIcon}/>
        );

        return (
            <>
                <AppButton onClick={() => {
                    this.props.toggleFavorite(itemId)
                }} className={classes.button}>
                    {favoriteIcon}
                </AppButton>
                <AppButton onClick={() => {
                    this.props.deleteItem(itemId)
                }} className={classes.button}>
                    <AppIcon icon={DeleteIcon}/>
                </AppButton>
                <AppButton onClick={this.props.editAction} className={classes.button}>
                    <AppIcon icon={EditIcon}/>
                </AppButton>
            </>
        );
    }
}

const mapDispatchToProps = dispatch => ({
    deleteItem: (id) => dispatch({type: 'DELETE_ITEM', id: id}),
    toggleFavorite: (id) => dispatch({type: 'TOGGLE_FAVORITE', id: id}),
});

export default connect(null, mapDispatchToProps)(CollectionItemListActions);
