import React, {Component} from 'react';
import {connect} from "react-redux";
import AppButton from "src/components/App/Button/AppButton";
import AppInput from "src/components/App/Input/AppInput";

import {ReactComponent as SaveIcon} from "src/assets/icons/check.svg";

import classes from "./CollectionItemEditForm.module.scss";

class CollectionItemEditForm extends Component {
    state = {
        title: "",
        description: ""
    };

    render() {
        return (
            <form onSubmit={this.save.bind(this)}>
                <h1>EDIT</h1>
                <AppInput name="title" placeholder={'Title'} value={this.state.title} onChange={this.handleTitleChane.bind(this)}/>
                <textarea name="description"
                          placeholder={'Description'}
                          value={this.state.description}
                          onChange={this.handleDescriptionChane.bind(this)}
                          className={classes.description}
                />
                <AppButton primary={1} type="submit" icon={SaveIcon}>
                    Save
                </AppButton>
            </form>
        );
    }

    getItem() {
        return this.props.item;
    }

    getData() {
        return this.getItem().data[0] || {};
    }

    componentDidMount() {
        const data = this.getData();
        this.setState({
            title: data.title,
            description: data.description
        });
    }

    handleTitleChane(e) {
        this.setState({
            title: e.target.value
        })
    }

    handleDescriptionChane(e) {
        this.setState({
            description: e.target.value
        })
    }

    save(e) {
        e.preventDefault();
        const item = this.getItem();
        item.data[0].title = this.state.title;
        item.data[0].description = this.state.description;
        this.props.editItem(item);
        this.props.onSave();
    }
}

const mapDispatchToProps = dispatch => ({
    editItem: (item) => dispatch({type: 'EDIT_ITEM', item: item}),
});

export default connect(null, mapDispatchToProps)(CollectionItemEditForm);